import styled from "styled-components";

const Wrapper = styled.section`
display:flex;
flex-direction:column;
>.output{
    background:white;
    font-size:36px;
    line-height:56px;
    text-align:right;
    padding:0 16px;
}
>.pad{
    >button{
        float: left;
        width:25%;
        height:48px;
        font-size:18px;
        border:none;
        &.ok{
            height:96px;
            float: right;
        }
        &.zero{
            width:50%;
        }
        &.dot{
            width:25%;
        }
        &:nth-child(1){
            background:rgba(90, 92, 201, 0.4);
        }
        &:nth-child(2),
        &:nth-child(5){
            background:rgba(90, 92, 201, 0.5);
        }
        &:nth-child(3),
        &:nth-child(6),
        &:nth-child(9){
            background:rgba(90, 92, 201, 0.6);
        } 
        &:nth-child(4),
        &:nth-child(7),
        &:nth-child(10),
        &:nth-child(13){
            background:rgba(90, 92, 201, 0.7);
        }
        &:nth-child(8),
        &:nth-child(11){
            background:rgba(90, 92, 201, 0.8);
        }
        &:nth-child(12){
            background:#5a5cc9;
        }
        &:nth-child(14){
            background:rgba(90, 92, 201, 0.9);
        }

    }
}

`;

export { Wrapper }