
import React, { useState } from "react";
import styled from "styled-components";
import Layout from "../components/Layout";
import { useRecord } from "../hooks/useRecord";
import { CategorySection } from "./moeny/CategorySection";
import { NotesSection } from "./moeny/NoteSection";
import { NumberSection } from "./moeny/NumberSection";
import { TagsSection } from "./moeny/TagsSection";



const MyLayout = styled(Layout)`
display: flex;
flex-direction:column;
`;
type Category = '-' | '+'

const defaultFormData = {
    tagIds: 1,
    note: '',
    category: '-' as Category,
    amount: 0,
}
function Money() {
    const [selected, setSelected] = useState(defaultFormData);
    const { addRecord } = useRecord()
    const onChange = (obj: Partial<typeof selected>) => {
        setSelected({ ...selected, ...obj });

    };
    const submit = () => {
        if (addRecord(selected)) {
            alert('保存成功');
            setSelected(defaultFormData);
        }
    };
    // console.log(selected.amount);
    return (
        <MyLayout>
            <CategorySection value={selected.category}
                onChange={category => onChange({ category })} />
            <TagsSection value={selected.tagIds}
                onChange={tagIds => onChange({ tagIds })} />
            < NotesSection value={selected.note}
                onChange={note => onChange({ note })} />
            <NumberSection value={selected.amount}
                onChange={amount => onChange({ amount })}
                onOK={submit}
            />
        </MyLayout >
    );
}
export default Money