
import styled from "styled-components";
import { useTags } from "../../hooks/useTag";

const Wrapper = styled.section`
background:#ffffff;
flex-grow:1;
flex-shrink:1;
height:10px;
overflow:scroll;
display:flex;
flex-direction:column;
align-items:flex-start;
::-webkit-scrollbar {
  display: none;
}
>ol{
    margin:0 -12px;
>li{
    background:rgba(90, 92, 201, 0.2);
border-radius:18px;
display:inline-block;
font-size:14px;
padding:4px 16px;
margin:10px 12px;
color:gray;
&.selected{
        color:black;
        background:rgba(90, 92, 201, 0.6)
    }
.icon{
        height:3em;
        width:3em;
       display:inline-block;
       
    }
}

}
>button{
    background:none;
    border:none;
    padding:0 4px;
    border-bottom:1px solid #333;
    color:#666;
    margin-top:8px;
}

`;
const Span = styled.div`
text-align:center;
`;
type Props = {
    value: number;
    onChange: (selected: number) => void

}
const TagsSection: React.FC<Props> = (props) => {
    const { tags, addTag } = useTags()
    const selectedTagIDs = props.value;

    const onToggleTag = (tagID: number) => {
        const index = (selectedTagIDs === tagID)
        if (!index) {
            props.onChange(tagID)
        }

    }
    const getClass = (tagId: number) => selectedTagIDs === tagId ? 'selected' : '';
    return (
        <Wrapper>
            <button onClick={addTag}>新增标签</button>
            <ol>
                {tags.map(tag =>
                    <li key={tag.id} onClick={
                        () => {
                            onToggleTag(tag.id)
                        }
                    }
                        className={getClass(tag.id)}
                    ><Span>{tag.name}</Span>
                    </li>
                )}
            </ol>

        </Wrapper>

    )

}
export { TagsSection }