import { useEffect, useState } from "react";
import { useUpdate } from "./useUpdate";
import { createID } from "../lib/createID";
import { useRecord } from "./useRecord";
const defaultTag = [
    { id: createID(), name: "消费" },
    { id: createID(), name: "餐饮" },
    { id: createID(), name: "购物" },
    { id: createID(), name: "住房" },
    { id: createID(), name: "交通" },
    { id: createID(), name: "其他" }
]
const useTags = () => { //自定义hook
    const [tags, setTags] = useState<{ id: number; name: string }[]>(defaultTag)
    const { updateRecord } = useRecord()
    useEffect(() => {
        let localTags = (JSON.parse(window.localStorage.getItem('tags') || '[]'))
        if (localTags.length === 0) {
            localTags = defaultTag
        }
        setTags(localTags)

    }, []);//组件挂载时执行
    useUpdate(() => {
        window.localStorage.setItem('tags', JSON.stringify(tags))
    }, tags)
    const findTag = (id: number) => tags.filter(tag => tag.id === id)[0]
    const findTagIndex = (id: number) => {
        let result = -1
        for (let i = 0; i < tags.length; i++) {
            if (tags[i].id === id) {
                result = tags[i].id;
                break
            }
        }
        return result
    }
    const updateTag = (id: number, obj: { name: string }) => {
        setTags(tags.map(tag => tag.id === id ? { id, name: obj.name } : tag
        ))
    }
    const deleteTag = (id: number) => {
        if (id === 1) {
            alert("该标签是记账本最基本的一个标签，请选择其他标签删除~")
        }
        else {
            if (window.confirm(`标签若有相关账目，删除后相关账单标签将改为默认标签。确定删除吗？`)) {
                setTags(tags.filter(tag => tag.id !== id))
                updateRecord(id)
            }
        }
    }
    const addTag = () => {
        const tagName = window.prompt("新标签为：")
        if (tagName !== null && tagName !== '') {
            setTags([...tags, { id: createID(), name: tagName }])
        }
        else {
            alert("标签名不能为空")
        }
    }
    const getName = (id: number) => {
        const tag = tags.filter(t => t.id === id)[0]
        return tag ? tag.name : ''
    }
    return { tags, setTags, findTag, updateTag, findTagIndex, deleteTag, addTag, getName }
}

export { useTags }

