import React, { useState } from "react";
import styled from "styled-components";
import Layout from "../components/Layout";
import { RecordItem, useRecord } from "../hooks/useRecord";
import { useTags } from "../hooks/useTag";
import { CategorySection } from "./moeny/CategorySection";
import day from "dayjs"
const CategoryWrapper = styled.div`
background:white;
`;
const Item = styled.div`
display: flex;
justify-content:space-between;
background: white;
font-size:20px;
padding:10px 16px;
align-items:center;
>.note{
    margin-right:auto;
    margin-left:16px;
    color:#999;
}
`;
const Header = styled.h3`
font-size:18px;
line-height:20px;
padding:10px 16px;
`;
const Detail = styled.div`
max-height:500px;
overflow:scroll;
::-webkit-scrollbar {
  display: none;
}
`;
function Statistics() {
    const [category, setCategory] = useState<'-' | '+'>('-')
    const { records } = useRecord()
    const { getName } = useTags()
    const hash: { [K: string]: RecordItem[] } = {}
    const selectRecord = records.filter(r => r.category === category)
    selectRecord.forEach(r => {
        const key = day(r.createAt).format("YYYY-MM-DD")
        if (!(key in hash)) {
            hash[key] = []
        }
        hash[key].push(r)
    })
    const array = Object.entries(hash).sort((a, b) => {
        if (a[0] === b[0]) return 0
        if (a[0] > b[0]) return -1
        if (a[0] < b[0]) return 1
        return 0
    })
    return (
        <Layout>
            <CategoryWrapper>
                <CategorySection value={category}
                    onChange={value =>
                        setCategory(value)}
                />
            </CategoryWrapper>
            <Detail>
                {array.map(([date, records], index) => {
                    return (
                        <div key={index}>
                            <Header>{date}</Header>
                            <div>
                                {records.map((r, index) => {
                                    return (
                                        <Item key={index}>
                                            <div className="tags">
                                                {getName(r.tagIds)}

                                            </div>
                                            <div className="note">
                                                {r.note}
                                            </div>
                                            <div className="amount">
                                                ￥{r.amount}
                                            </div>
                                        </Item>
                                    )
                                })}
                            </div>
                        </div>
                    )
                })}
            </Detail>
        </Layout>
    )
        ;
}
export default Statistics